import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Table, Col, Button } from 'antd';
import { BtnSave } from 'components/common/button';
import { xetduyetcanhan } from 'dump/data';
import FormSerchIndividual from './FormSerchIndividual';

const columns = [
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="primary">Chọn</Button>
      </Fragment>
    ),
  },
  {
    title: 'Mã SV',
    dataIndex: 'masv',
    key: 'name',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'nameEn',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'fromScores',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'toScores',
  },
  {
    title: 'TBCHT4',
    dataIndex: 'tbcht4',
    key: 'toScores',
  },
  {
    title: 'TBCHT10',
    dataIndex: 'tbcht10',
    key: 'toScores',
  },
  {
    title: 'Xếp loại HT',
    dataIndex: 'ht',
    key: 'toScores',
  },
  {
    title: 'Tổng điểm RL',
    dataIndex: 'rl',
    key: 'toScores',
  },
  {
    title: 'Xếp loại RL',
    dataIndex: 'typeRL',
    key: 'toScores',
  },
  {
    title: 'Danh hiệu',
    dataIndex: 'danhhieu',
    key: 'toScores',
  },
];

const columnsTableChooes = [
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="danger">Xóa</Button>
      </Fragment>
    ),
  },
  {
    title: 'Mã SV',
    dataIndex: 'masv',
    key: 'name',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'nameEn',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'fromScores',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'toScores',
  },
  {
    title: 'TBCHT4',
    dataIndex: 'tbcht4',
    key: 'toScores',
  },
  {
    title: 'TBCHT10',
    dataIndex: 'tbcht10',
    key: 'toScores',
  },
  {
    title: 'Xếp loại HT',
    dataIndex: 'ht',
    key: 'toScores',
  },
  {
    title: 'Tổng điểm RL',
    dataIndex: 'rl',
    key: 'toScores',
  },
  {
    title: 'Xếp loại RL',
    dataIndex: 'typeRL',
    key: 'toScores',
  },
  {
    title: 'Danh hiệu',
    dataIndex: 'danhhieu',
    key: 'toScores',
  },
];

class IndividualPage extends Component {
  state = {};

  render() {
    return (
      <Fragment>
        <Card title="Danh hiệu thi đua cá nhân">
          <Row>
            <Col span={24}>
              <FormSerchIndividual />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table bordered dataSource={xetduyetcanhan} columns={columns} />
            </Col>
          </Row>
        </Card>
        <Card title="Danh sách đã chọn">
          <Row>
            <Col span={24}>
              <Table bordered dataSource={xetduyetcanhan} columns={columnsTableChooes} />
            </Col>
            <Col offset={8} span={12}>
              <BtnSave htmlType="submit" />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <IndividualPage />
  </Page>
);



