import { request } from 'utils';

const prefix = '/api/students';

export const getClasss = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Class',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });



